<template>
    <main class="product">
        <Carousel :images="product.content.images" />
        <div class="content">
            <h1 class="title title--primary">{{ product.content.name }}</h1>
            <p class="price">{{ product.inventory.price }}</p>
            <div class="add">
                <label for="quantity"></label>
                <input type="number" id="quantity" name="quantity">
                <button>Add to cart</button>
            </div>
            <hr>
            <h2 class="title title--secondary">Description</h2>
            <p class="description text">
                {{ product.content.description }}
            </p>
        </div>
    </main>
</template>

<script>
    import Carousel from "@/components/content-sections/Carousel.vue"

    export default {
        name: "product-view",
        props: ["category", "product"],
        components: {
            Carousel
        },
        data() {
            return {
                product: null
            }
        },
        mounted() {
            this.$axios.get("/products/" + this.category + "/" + this.product)
            .then((res) => {
                console.log(res)
                this.product = res.data.product
            })
        }
    }
</script>

<style lang="scss" scoped>
    .product {
        @include mq("tablet") {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        .title--primary {
            font-size: 3em;
        }
    }
</style>